import Vue from "vue"
import App from "./App"
import {
  sync
} from 'vuex-router-sync'
require('./utils/vconsole.js')
import router from "./router"
import VueAwesomeSwiper from 'vue-awesome-swiper'
import http from "./utils/axios"
import convertEmotions from './utils/convertEmotions'
import store from "@/store/store"
import * as filters from './filters'
import "swiper/dist/css/swiper.css"
import "./assets/css/common.css"
import "./assets/css/index.css"
import "./assets/css/animate.css"
import FastClick from "fastclick"
import 'lib-flexible'
import 'mint-ui/lib/style.css'
import { Toast, Indicator, Header, Button, MessageBox, CellSwipe, Cell} from 'mint-ui'
import Mixins from './mixins'
import scrollEvent from './utils/scroll'
import debounce from './utils/debounce'
import { productURL, testURL } from './config/export.config'
import './directive'
import Loading from './plugins/loading'
import addEvent from './utils/addEvent'
import offsetTop from './utils/offsetTop'
import Photoswipe from "vue-pswipe"
import vuePicturePreview from 'vue-picture-preview'
Vue.use(vuePicturePreview)
Vue.use(Photoswipe)
import './assets/css/viewer.css'
import Viewer from 'v-viewer'
Vue.use(Viewer)

Vue.component(Cell.name, Cell)
Vue.component(CellSwipe.name, CellSwipe)
Vue.component(Button.name, Button)
Vue.component(Header.name, Header)
Vue.use(Loading)
Vue.use(VueAwesomeSwiper)
// FastClick.attach(document.body)

Vue.prototype.$Toast = Toast
Vue.prototype.$Indicator = Indicator
Vue.prototype.$MessageBox = MessageBox
Vue.prototype.productURL = productURL
Vue.prototype.convertEmotions = convertEmotions
Vue.prototype.scrollEvent = scrollEvent
Vue.prototype.axios = http
Vue.prototype.debounce = debounce
Vue.prototype.addEvent = addEvent
Vue.prototype.offsetTop = offsetTop

http.interceptors.response.use( config => {
  return config
}, error => {
  for (const key in error) {
    if(error[key] === 'ECONNABORTED'){
      Vue.prototype.$Indicator.close()
      Vue.prototype.$Toast({
        message: ', ',
        position: 'middle',
        duration: 1000
      })
    }
  }
})

Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})

router.beforeEach((to, from, next) => {

  store.commit('modifyPreviewerPosition', 0)

  //  => 
  window.__axiosPromiseArr.forEach((ele, index) => {
    ele.cancel()
    delete window.__axiosPromiseArr[index]
  })
  Vue.prototype.$Indicator.close()
  // 

  let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0
  let route = from.fullPath

  if(route.includes('highestHeat')){
    store.state.workRangeSavePosition.highestHeat = scrollTop
  }else if(route.includes('latestTime')){
    store.state.workRangeSavePosition.latestTime = scrollTop
  }else if(route.includes('workRelatedMe')){
    store.state.workRangeSavePosition.workRelatedMe = scrollTop
  }else if(route.includes('myWorkInRange')){
    store.state.workRangeSavePosition.myWorkInRange = scrollTop
  }

  // 

  if (to.meta.title) {
    document.title = to.meta.title
  }

  // 
  
  setTimeout(() => {
    var _hmt = _hmt || [];
  
    (function () {
      document.getElementById("baidu_tj") &&
        document.getElementById("baidu_tj").remove()
      var hm = document.createElement("script")
      hm.src = "https://hm.baidu.com/hm.js?39bb7435f11327746847c7561c6067bb"
      hm.id = "baidu_tj"
      var s = document.getElementsByTagName("script")[0]
      s.parentNode.insertBefore(hm, s)
    })()
  }, 0)

  next()
  
})

Vue.mixin(Mixins)

sync(store, router)

new Vue({
  el: "#app",
  router,
  store,
  template: "<App/>",
  components: {
    App
  },
  mounted() {

      if(!localStorage.getItem('dapeng')) return

      this.axios.post("/api/logined").then(data => {
      if (data.data.content == "") {
        return
      }
      if(data.data.status === 0) {
        localStorage.removeItem('dapeng')
        store.state.userModule.USER_INFO_OBJECT = null
        store.state.userModule.LOGIN_USER_OBJECT = null
        return
      }
      
      let user = data.data.user
      if(user.clueCategory !== '-1' || (user.clueCategory === '-1' && (user.isAdministrator === 'Y' || user.isInstructor === 'Y'))){
        
      }else{
        localStorage.removeItem('dapeng')
        store.state.userModule.USER_INFO_OBJECT = null
        store.state.userModule.LOGIN_USER_OBJECT = null
        return
      }
      if(!data.data.user.avatar.startsWith('http')){
        data.data.user.avatar = `${store.state.link}${data.data.user.avatar}`
      }
      store.state.userModule.LOGIN_USER_OBJECT = data.data
    })
    
    if(store.state.newsStatus == null){
      this.axios.post("/api/getUnReadReply").then(data => {
        if (data.data.content == "") {
          store.state.newsStatus = 0
          return
        }
        store.state.newsStatus = data.data.result.unReadSize
      })
    }
  }
})



