import Vue from "vue"
import Router from "vue-router"
import App from "@/App"
import mineRouter from './mine'
import workCircleRouter from './workCircle'
import commonRouter from './commonRouter'
import workAreaRouter from './workArea'
import newsRouter from './news'
import homeRouter from './home'
import courseRouter from './course'
import liveRouter from './live'
import errRouter from './err'
import ProgressBar from '@/components/progressBar'
import store from './../store/store'

Vue.use(Router)

let router = new Router({
  linkActiveClass: "active",
  linkExactActiveClass: "active",
  // mode: "history",
  scrollBehavior(to, from, savedPosition) {
    return { x: 0, y: 0 }
  },
  routes: [
    {
      path: "/",
      component: App,
      children: [
        homeRouter
      ]
    },
    ...courseRouter,
    ...mineRouter,
    ...workCircleRouter,
    ...commonRouter,
    ...workAreaRouter,
    ...newsRouter,
    ...liveRouter,
    ...errRouter
  ]
})

let loading = Vue.prototype.$loading = new Vue(ProgressBar).$mount()

document.body.appendChild(loading.$el)

// 
router.beforeResolve((to, from, next) => {
  let matched = router.getMatchedComponents(to)
  let prevMatched = router.getMatchedComponents(from)

  let diffed = false
  let activated = matched.filter((c, i) => diffed || (diffed = (prevMatched[i] !== c)))

  if (!activated.length) {
      return next()
  }

  loading.start()
  Promise.all(activated.map(c => {

      /**
       * asyncData:
       * 1. keep-alive
       * 2. keep-alive
       */
      if (c.asyncData && (!c.asyncDataFetched || to.meta.notKeepAlive)) {
          return c.asyncData({
              store,
              route: to
          }).then(() => {
              c.asyncDataFetched = true
          })
      }
  })).then(() => {
      loading.finish()
      next()
  }).catch(next)
})

export default router



