export default [
    {
        path: "/sendNews/:id/:name",
        name: "sendNews",
        component: () => import("@/pages/home/myNews/sendNews"),
        meta: {
          title: ''
        },
      },
      {
        path: "/myNews",
        name: "myNews",
        component: () => import("@/pages/home/myNews/myNews"),
        meta: {
          title: ''
        },
        beforeEnter: (to, from, next) => {
          if(!localStorage.getItem('dapeng')){
            next('/mine')
          }else{
            next()
          }
        }
      },
      {
        path: "/newsList/:anotherUserId/:noteId",
        name: "newsList",
        component: () => import("@/pages/home/myNews/newsList"),
        meta: {
          title: ''
        },
      },
]


