
import emotionslist from './emojiJson'

let convertEmotions = function (html) {
    let replaceHtml = html.replace(/\[.*?\]/g, function (title) {
        emotionslist.forEach(element => {
            element.forEach(ele => {
                if (`[${ele.title}]` == title) {
                title = `<img src="${ele.url}" alt="${ele.title}">`
                }
            })
        })
        return title
    })
    return replaceHtml
}

export default convertEmotions


