import * as types from './mutation-types'
import axios from '../../utils/axios'
import { WORK_RANGE_HEAT_OBJECT, WORK_RANGE_COMM_OBJECT, WORK_RANGE_ABOUT_OBJECT, WORK_RANGE_MINE_OBJECT, WORK_PARISE } from '../../api/work-area-api'
import { productURL } from '../../config/export.config'

let actions = {
	// 
	async [types.WORK_RANGE_HEAT_OBJECT]({ commit }, info) {
		try {
			let data = await axios.post(WORK_RANGE_HEAT_OBJECT, info)
			commit('WORK_RANGE_HEAT', data)
			return data
		} catch (error) {
			console.log(error)
		}
	},
	// 
	async [types.WORK_RANGE_COMM_OBJECT]({ commit }, info) {
		try {
			let data = await axios.post(WORK_RANGE_COMM_OBJECT, info)
			commit('WORK_RANGE_COMM', data)
			return data
		} catch (error) {
			console.log(error)
		}
	},
	// 
	async [types.WORK_RANGE_ABOUT_OBJECT]({ commit }, info) {
		try {
			let data = await axios.post(WORK_RANGE_ABOUT_OBJECT, info)
			if (data.data.result.list && data.data.result.list.length > 0) {
				data.data.result.list.forEach(element => {
					if (!element.avatar.startsWith('http')) {
						element.avatar = `${productURL}${element.avatar}`
					}
				})
			}
			commit('WORK_RANGE_ABOUT', data)
			return data
		} catch (error) {
			console.log(error)
		}
	},
	// 
	async [types.WORK_RANGE_MINE_OBJECT]({ commit }, info) {
		try {
			let data = await axios.post(WORK_RANGE_MINE_OBJECT, info)
			commit('WORK_RANGE_MINE', data)
			return data
		} catch (error) {
			console.log(error)
		}
	},
	async [types.WORK_PARISE]({ commit }, info){
		try{
			let data = await axios.post(WORK_PARISE, info)
			return data
		} catch (error) {
			console.log(error)
		}
	}
}

export default actions



