export default [
  {
      path: "/workComment",
      name: "workComment",
      component: () => import("@/pages/mine/workComment"),
      meta: {
          title: ''
      }
    },
    {
      path: "/myWorkDetail/:courseId/:title",
      name: "myWorkDetail",
      component: () => import("@/pages/mine/myWorkDetail"),
      meta: {
        title: ''
      },
    },
    {
      path: "/workRe/:taskId/:title",
      name: "workRe",
      component: () => import("@/pages/mine/workRe"),
      meta: {
        title: ''
      },
    },
    {
      path: "/workRangeDetail/:taskId/:jobcompletionId",
      name: "workRangeDetail",
      component: () => import("@/pages/workArea/workRangeDetail"),
      meta: {
        title: ''
      },
    },
    {
      path: "/submitingWork",
      name: "submitingWork",
      component: () => import("@/pages/workArea/submitingWork"),
      meta: {
        title: ''
      },
      beforeEnter: (to, from, next) => {
        if(!localStorage.getItem('dapeng')){
          next('/mine')
        }else{
          next()
        }
      }
    },
    {
      path: "/publishWork/:taskId/:statu",
      name: "publishWork",
      component: () => import("@/pages/workArea/publishWork"),
      meta: {
        title: ''
      },
    },
    {
      path: "/workArea/:id",
      name: "workArea",
      component: () => import("@/pages/workArea/workArea"),
      meta: {
        title: ''
      },
      children: [
          {
            path: "latestTime",
            name: "latestTime",
            component: () => import("@/pages/workArea/lastestTime"),
            meta: {
              title: ''
            },
          },
          {
            path: "highestHeat",
            name: "highestHeat",
            component: () => import("@/pages/workArea/highestHeat"),
            meta: {
              title: ''
            },
          },
          {
            path: "myWorkInRange",
            name: "myWorkInRange",
            component: () => import("@/pages/workArea/myWorkInRange"),
            meta: {
              title: ''
            },
          },
          {
            path: "workRelatedMe",
            name: "workRelatedMe",
            component: () => import("@/pages/workArea/workRelatedMe"),
            meta: {
              title: ''
            },
          }
      ]
    },
    {
      path: "/replayComment/:commentId/:jobcompletionId",
      name: "replayComment",
      component: () => import("@/pages/workArea/replayComment"),
      meta: {
        title: ''
      },
    },
    
]


