import * as types from './mutation-types'
import { productURL, testURL } from '../../config/export.config'

let mutations = {
    [types.LOGIN_USER_OBJECT](state, info) {
        if (!info.data.content.result.user.avatar.startsWith('http')) {
            info.data.content.result.user.avatar = `${productURL}/${info.data.content.result.user.avatar}`
        }

        if (info.data.content.result.user.address) {
            info.data.content.result.user.address = info.data.content.result.user.address.replace(/@/, ' ')
        }
        
        let result = info.data.content.result

        if (result.user.clueCategory !== '-1' || (result.user.clueCategory === '-1' && (result.user.isAdministrator === 'Y' || result.user.isInstructor === 'Y'))) {
            state['LOGIN_USER_OBJECT'] = info.data.content.result
        } else {
            state['VALID_USER_OBJECT'] = info.data.content.result
        }
    },
    GET_LOGINED(state, info){
        state.LOGIN_USER_OBJECT = info.data
    },
    [types.USER_INFO_OBJECT](state, info) {
        if (info.data.status === 0) return
        // 
        if (info.data.user.gender === 'S') {
            info.data.user.gender = ''
        } else if (info.data.user.gender === 'F') {
            info.data.user.gender = ''
        } else if (info.data.user.gender === 'M') {
            info.data.user.gender = ''
        }

        // 
        if (!info.data.user.avatar.startsWith('http')) {
            info.data.user.avatar = `${productURL}/${info.data.user.avatar}`
        }
        // 
        if (info.data.user.address) {
            info.data.user.address = info.data.user.address.replace(/@/g, ' ')
        }

        state['USER_INFO_OBJECT'] = info.data.user
    },
    [types.MULTI_USER_OBJECT](state, info) {
        state['MULTI_USER_OBJECT'] = info
    },
    [types.VALID_USER_OBJECT](state, info) {
        state['VALID_USER_OBJECT'] = info
    },
    [types.MOBILE_CODE_OBJECT](state, info) {
        if (info.data.result !== 'failure') {
            state['MOBILE_CODE_OBJECT'] = info
        }
    },
    BIND_USER(state, info) {
        state['LOGIN_USER_OBJECT'] = info
    },
    GET_COMPARE_MOBILECODE: function (state, info) {
        // 
        if (!info.data.content.user.avatar.startsWith('http')) {
            info.data.content.user.avatar = `${productURL}/${info.data.content.user.avatar}`
        }
        // 
        if (info.data.content.user.address) {
            info.data.content.user.address = info.data.content.user.address.replace(/@/g, ' ')
        }
        state['LOGIN_USER_OBJECT'] = info.data.content
    },
    CHECK_REALMOBILE: function (state, info) {
        // 
        if (!info.data.content.user.avatar.startsWith('http')) {
            info.data.content.user.avatar = `${productURL}/${info.data.content.user.avatar}`
        }
        // 
        if (info.data.content.user.address) {
            info.data.content.user.address = info.data.content.user.address.replace(/@/g, ' ')
        }
        state['LOGIN_USER_OBJECT'] = info.data.content
    },
    MODAL_REGISTER: function (state, info) {
        // 
        if (!info.data.content.user.avatar.startsWith('http')) {
            info.data.content.user.avatar = `${productURL}/${info.data.content.user.avatar}`
        }
        // 
        if (info.data.content.user.address) {
            info.data.content.user.address = info.data.content.user.address.replace(/@/g, ' ')
        }
        state['LOGIN_USER_OBJECT'] = info.data.content
    },
    SELECT_MUTIACCOUNT: function (state, info) {
        if (!info.content.user.avatar.startsWith('http')) {
            info.content.user.avatar = `${productURL}/${info.content.user.avatar}`
        }
        state['LOGIN_USER_OBJECT'] = info.content
    },
    GET_USER_SECURITY: function (state, info) {
        state['USER_SECURITY_OBJECT'] = info.data.result
    },
    // 
    GET_MYFANCE: function (state, info) {
        if (info.data.contents.length) {
            info.data.contents.forEach(element => {
                if (!element.avatar.startsWith('http')) {
                    element.avatar = `${state.LINK}${element.avatar}`
                }
            });
        }
        state.MY_FANCE_OBJECT_LIST.MY_FANCE_LIST = state.MY_FANCE_OBJECT_LIST.MY_FANCE_LIST.concat(info.data.contents)
    },
    // 
    GET_MYFRIEND: function(state, info){
        if(info.data.contents.length){
          info.data.contents.forEach(element => {
            if(!element.avatar.startsWith('http')){
              element.avatar = `${state.LINK}${element.avatar}`
            }
          });
        }
        state.MY_FRIEND_OBJECT_LIST.MY_FRIEND_LIST = state.MY_FRIEND_OBJECT_LIST.MY_FRIEND_LIST.concat(info.data.contents)
      },
    //
    [types.PINGLUN_DEL](state, info) {
        state['PINGLUN_DEL'] = info
    },
    //
    [types.GET_COMMENT_LIST](state, info) {
        state['GET_COMMENT_LIST'] = info
    },
    //
    [types.GET_JOB_DETAILS](state, info) {
        state['GET_JOB_DETAILS'] = info
    },
    //
    [types.TASK_DETAILS](state, info) {
        state['TASK_DETAILS'] = info
    },
    //
    [types.GET_JOBLIST_BY_TASKID](state, info) {
        state['GET_JOBLIST_BY_TASKID'] = info
    },
}

export default mutations



