import * as types from './mutation-types'
import axios from '../../utils/axios'
import { MINE_API, 
  FIND_USER_API, 
  SEND_CAPTCHA_API, 
  LOGINED_API, 
  SELECT_MUT_USER_API, 
  GET_MY_FRIEND_API, 
  GET_MY_FANCE_API, 
  RESET_PASSWORD_API, 
  COMPARE_ACCOUNT_API, 
  CHECK_REAL_MOBILE_API,
  REGISTER_API,
  MUTI_LOGIN_API,
  USER_SECURITY_API,
  PINGLUN_DEL,
  GET_COMMENT_LIST,
  GET_JOB_DETAILS,
  TASK_DETAILS,
  GET_JOBLIST_BY_TASKID } from '../../api/userinfo-api'

let actions = {
  async [types.LOGIN_USER_OBJECT]({ commit }, info) {
    try {
      let data = await axios.post(MINE_API, info)
      if(data.data && Array.isArray(data.data)){
        commit('MULTI_USER', data.data)
        return data
      }else if(data.data.content !== ''){
        commit('LOGIN_USER', data)
        return data
      }else if(data.data.content === ''){
        return {
          content: '',
          status: 0
        }
      }
    } catch (error) {
      console.log(error)
    }

  },
  async [types.USER_INFO_OBJECT]({ commit }, info) {
    try {
      let data = await axios.get(FIND_USER_API, info)
      commit('USER_INFO', data)
      return data
    } catch (error) {
      console.log(error)
    }
  },
  async [types.MOBILE_CODE_OBJECT]({ commit }, info) {
    try {
      let data = await axios.post(SEND_CAPTCHA_API, info)
      commit('MOBILE_CODE', data)
      return data
    } catch (error) {
      console.log(error)
    }
  },
  // 
  async GET_MOBILE_CODE(state, info){
    try {
      let data = await axios.post(SEND_CAPTCHA_API, info)
      return data
    } catch (error) {
      console.log(error)
    }
  },
  // 
  async GET_LOGINED({ commit }){
    try {
      if(!localStorage.getItem('dapeng')) return

      let data  = await axios.post( LOGINED_API )

      if(data.data.status === 0) {
        localStorage.removeItem('dapeng')
        store.state.userModule.USER_INFO_OBJECT = null
        store.state.userModule.LOGIN_USER_OBJECT = null
        return
      }

      let user = data.data.user
      if(user.clueCategory !== '-1' || (user.clueCategory === '-1' && (user.isAdministrator === 'Y' || user.isInstructor === 'Y'))){
        
      }else{
        localStorage.removeItem('dapeng')
        store.state.userModule.USER_INFO_OBJECT = null
        store.state.userModule.LOGIN_USER_OBJECT = null
        return
      }
      if(!data.data.user.avatar.startsWith('http')){
        data.data.user.avatar = `${store.state.link}${data.data.user.avatar}`
      }
      commit('GET_LOGINED', data)
      return data
    } catch (error) {
      console.log(error)
    }
  },
  // 
  async SELECT_LOGIN_ACTION({ commit }, info){
    try {
      let data = await axios.get(SELECT_MUT_USER_API, info)
      commit('SELECT_LOGIN_ACTION', data.data)
      return data
    } catch (error) {
      console.log(error)
    }

  },
  // 
  async GET_MYFRIEND({ commit }, info){
    try {
      let data = await axios.get(GET_MY_FRIEND_API, info)
      commit('GET_MYFRIEND', data)
      return data
    } catch (error) {
      console.log(error)
    }
  },
  // 
  async GET_MYFANCE({ commit }, info){
    try {
      let data = await axios.get(GET_MY_FANCE_API, info)
      commit('GET_MYFANCE', data)
      return data
    } catch (error) {
      console.log(error)
    }
  },
  // [/], 
  async MODIFIED_USER_PWD(state, info){
    try {
      let data = await axios.post(RESET_PASSWORD_API, info)
      return data
    } catch (error) {
      console.log(error)
    }
  },
  // 
  async GET_COMPARE_MOBILECODE({ commit }, info){
    try {
      let data = await axios.post(COMPARE_ACCOUNT_API, info)
      if(data.data.status !== 0){
        commit('GET_COMPARE_MOBILECODE', data)
      }
      return data
    } catch (error) {
      console.log(error)
    }
  },
  // 
  async CHECK_REALMOBILE({ commit }, info){
    try {
      let data = await axios.post(CHECK_REAL_MOBILE_API, info)
      commit('CHECK_REALMOBILE', data)
      return data
    } catch (error) {
      console.log(error)
    }

  },
  // 
  async MODAL_REGISTER({ commit }, info){
    try {
      let data = await axios.post(REGISTER_API, info)
      if(data.data.status !== 'failure'){
        commit('MODAL_REGISTER', data)
      }
      return data
    } catch (error) {
      console.log(error)
    }
  },
  // 
  async SELECT_MUTIACCOUNT({ commit }, info){
    try {
      let data = await axios.get(MUTI_LOGIN_API, info)
      commit('SELECT_MUTIACCOUNT', data.data)
      return data 
    } catch (error) {
      console.log(error)
    }
  },
  // 
  async GET_USER_SECURITY(state){
    try {
      let data = await axios.post( USER_SECURITY_API )
      return data
    } catch (error) {
      console.log(error)
    }
  },
  //
  async PINGLUN_DEL({ commit }, info){
    try {
      let data = await axios.post(PINGLUN_DEL, info)
      commit('PINGLUN_DEL', data)
      return data
    } catch (error) {
      console.log(error)
    }
  },
  //
  async GET_COMMENT_LIST({ commit }, params){
    try {
      let data = await axios.get(GET_COMMENT_LIST, { params })
      commit(types.GET_COMMENT_LIST, data)
      return data
    } catch (error) {
      console.error(error)
    }
  },
  //
  async GET_JOB_DETAILS({ commit }, info){
    try {
      let data = await axios.post(GET_JOB_DETAILS, info)
      commit('GET_JOB_DETAILS', data)
      return data
    } catch (error) {
      console.log(error)
    }
  },
  //
  async TASK_DETAILS({ commit }, info){
    try {
      let data = await axios.post(TASK_DETAILS, info)
      commit('TASK_DETAILS', data)
      return data
    } catch (error) {
      console.log(error)
    }
  },
  //
  async GET_JOBLIST_BY_TASKID({ commit }, params){
    try {
      let data = await axios.get(GET_JOBLIST_BY_TASKID, { params })
      commit('GET_JOBLIST_BY_TASKID', data)
      return data
    } catch (error) {
      console.log(error)
    }
  },
}

export default actions



