import axios from 'axios'
import {WIFIURL, testURL, localURL, BPORT} from '../config/export.config'

const PORT = BPORT
const HOST = testURL

let http = axios.create({
    timeout: 20000,
    baseURL: `${HOST}:${PORT}`
})

window.__axiosPromiseArr = []

http.interceptors.request.use(
    function (config) {
        config.headers["x-access-token"] = localStorage.getItem("dapeng")
        config.cancelToken = new axios.CancelToken(cancel => {
            window.__axiosPromiseArr.push({cancel})
        })
        return config
    },
    function (error) {
        return Promise.reject(error)
    }
)

export default http


