export default [
  {
  path: "/live/:id/:courseId",
  name: "live",
  component: () =>
    import ("@/pages/live/live"),
  meta: {
    title: ''
  }
},{
  path: "/live_vod/:id/:vid",
  name: "live_vod",
  component: () =>
    import ("@/pages/live/liveCurr"),
  meta: {
    title: ''
  }
}]



