let numFm = info => {
    let size = (info ? info : '').toString().length
    if (size > 4) {
        if ((info / 10000).toString().indexOf(".") !== -1) {
        return `${(info / 10000).toString().split(".")[0]}.${(info / 10000)
            .toString()
            .split(".")[1]
            .charAt(0)}w`
        } else {
        return `${(info / 10000).toString()}w`
        }
    } else if (size == 4) {
        if ((info / 1000).toString().indexOf(".") !== -1) {
        return `${(info / 1000).toString().split(".")[0]}.${(info / 1000)
            .toString()
            .split(".")[1]
            .charAt(0)}k`
        } else {
        return `${(info / 1000).toString()}k`
        }
    } else {
        return info
    }
}

let dateFm = dateTimeStamp => {
    let timestamp = new Date(dateTimeStamp)
    let getFullYear = timestamp.getFullYear()
    let getMonth =(timestamp.getMonth() + 1).toString().padStart(2, '0')
    let getDay = timestamp.getDate().toString().padStart(2, '0')
    let getHour = timestamp.getHours().toString().padStart(2, '0')
    let getMinutes = timestamp.getMinutes().toString().padStart(2, '0')
    let getSeconds = timestamp.getSeconds().toString().padStart(2, '0')
    return `${getFullYear}-${getMonth}-${getDay} ${getHour}:${getMinutes}:${getSeconds}`
}

let monthFm = dateTimeStamp => {
    let timestamp = new Date(dateTimeStamp)
    let getMonth =(timestamp.getMonth() + 1).toString().padStart(2, '0')
    let getDay = timestamp.getDate().toString().padStart(2, '0')
    let getHour = timestamp.getHours().toString().padStart(2, '0')
    let getMinutes = timestamp.getMinutes().toString().padStart(2, '0')
    let getSeconds = timestamp.getSeconds().toString().padStart(2, '0')
    return `${getMonth}-${getDay} ${getHour}:${getMinutes}:${getSeconds}`
}


let datePoint = dateTimeStamp => {
    let timestamp = new Date(dateTimeStamp)
    let getFullYear = timestamp.getFullYear()
    let getMonth =(timestamp.getMonth() + 1).toString().padStart(2, '0')
    let getDay = timestamp.getDate().toString().padStart(2, '0')
    let getHour = timestamp.getHours().toString().padStart(2, '0')
    let getMinutes = timestamp.getMinutes().toString().padStart(2, '0')
    let getSeconds = timestamp.getSeconds().toString().padStart(2, '0')
    return `${getFullYear}.${getMonth}.${getDay} ${getHour}:${getMinutes}`
}

let dateDiff = dateTimeStamp => {
    let result = ""
    let minute = 1000 * 60
    let hour = minute * 60
    let day = hour * 24
    let halfamonth = day * 15
    let month = day * 30
    let now = new Date().getTime()
    let diffValue = now - dateTimeStamp
    if (diffValue < 0) {
        return
    }
    let monthC = diffValue / month
    let weekC = diffValue / (7 * day)
    var dayC = diffValue / day
    let hourC = diffValue / hour
    let minC = diffValue / minute
    if (monthC >= 1) {
        result = "" + parseInt(monthC) + ""
    } else if (weekC >= 1) {
        result = "" + parseInt(weekC) + ""
    } else if (dayC >= 1) {
        result = "" + parseInt(dayC) + ""
    } else if (hourC >= 1) {
        result = "" + parseInt(hourC) + ""
    } else if (minC >= 1) {
        result = "" + parseInt(minC) + ""
    } else result = ""
    return result
}

let dateDiffTimestamp = dateTimeStamp => {
    let result = ""
    let minute = 1000 * 60
    let hour = minute * 60
    let day = hour * 24
    let halfamonth = day * 15
    let month = day * 30
    let year = month * 12
    let now = new Date().getTime()
    let diffValue = now - dateTimeStamp
    if (diffValue < 0) {
        return
    }
    let yearC = diffValue / year
    let monthC = diffValue / month
    let weekC = diffValue / (7 * day)
    let dayC = diffValue / day
    let hourC = diffValue / hour
    let minC = diffValue / minute
    let timestamp = new Date(dateTimeStamp)
    if(yearC >= 1){
        result =  `${(timestamp.getFullYear())}-${(timestamp.getMonth() + 1).toString().padStart(2, '0')}-${(timestamp.getDate()).toString().padStart(2, '0')} ${(timestamp.getHours()).toString().padStart(2, '0')}:${(timestamp.getMinutes()).toString().padStart(2, '0')}:${(timestamp.getSeconds()).toString().padStart(2, '0')}`
    }else if (monthC >= 1) {
        result =  `${(timestamp.getMonth() + 1).toString().padStart(2, '0')}-${(timestamp.getDate()).toString().padStart(2, '0')} ${(timestamp.getHours()).toString().padStart(2, '0')}:${(timestamp.getMinutes()).toString().padStart(2, '0')}:${(timestamp.getSeconds()).toString().padStart(2, '0')}`
    } else if (weekC >= 1) {
        result =  `${(timestamp.getMonth() + 1).toString().padStart(2, '0')}-${(timestamp.getDate()).toString().padStart(2, '0')} ${(timestamp.getHours()).toString().padStart(2, '0')}:${(timestamp.getMinutes()).toString().padStart(2, '0')}:${(timestamp.getSeconds()).toString().padStart(2, '0')}`
    } else if (dayC >= 1) {
        result =  `${(timestamp.getMonth() + 1).toString().padStart(2, '0')}-${(timestamp.getDate()).toString().padStart(2, '0')} ${(timestamp.getHours()).toString().padStart(2, '0')}:${(timestamp.getMinutes()).toString().padStart(2, '0')}:${(timestamp.getSeconds()).toString().padStart(2, '0')}`
    } else if (hourC >= 1) {
        result = "" + parseInt(hourC) + ""
    } else if (minC >= 1) {
        result = "" + parseInt(minC) + ""
    } else result = ""
    return result
}

let ddFm = dateTimeStamp => {
    let date = new Date(dateTimeStamp).getDate().toString().padStart(2, '0')
    return date
}

let mmFm = dateTimeStamp => {
    let date = (new Date(dateTimeStamp).getMonth() + 1).toString().padStart(2, '0')
    return date
}

export { numFm, dateFm, datePoint, dateDiff, dateDiffTimestamp, ddFm, mmFm, monthFm }



