export default [
    {
        path: "/myWorkCircleCenter",
        name: "myWorkCircleCenter",
        component: () => import("@/pages/workCircle/myWorkCircle"),
        meta: {
          title: ''
        }
      },
      
    {
        path: "/publishWorkCircle/:id",
        name: "publishWorkCircle",
        component: () => import("@/pages/workCircle/publishWorkCircle"),
        meta: {
          title: ''
        }
      },
      
    {
        path: "/circleDetail/:id",
        name: "circleDetail",
        component: () => import("@/pages/workCircle/workCircleDetail"),
        meta: {
          title: ''
        },
        children: [
          {
            path: "workCircleCommentList",
            name: "workCircleCommentList",
            component: () =>
              import("@/pages/workCircle/workCircleCommentList")
          },
          {
            path: "workCirclePraiseList",
            name: "workCirclePraiseList",
            component: () =>
              import("@/pages/workCircle/workCirclePraiseList")
          }
        ]
      },
      {
        path: "/addWork/:id",
        name: "addWork",
        component: () => import("@/pages/workCircle/publishWorkCircle"),
        meta: {
          title: ''
        }
      },
      {
        path: "/workCircle",
        name: "workCircle",
        component: () => import("@/pages/workCircle/workCircle"),
        meta: {
          title: ''
        },
      },
]


