import { WORK_RANGE_HEAT_OBJECT, WORK_RANGE_COMM_OBJECT, WORK_RANGE_ABOUT_OBJECT, WORK_RANGE_MINE_OBJECT } from './mutation-types'

let state = {
    WORK_RANGE_HEAT_OBJECT: {
        WORK_HEAT_LIST: [],
        WORK_HEAT_PAGE: 1
    },
    WORK_RANGE_COMM_OBJECT: {
        WORK_COMM_LIST: [],
        WORK_COMM_PAGE: 1
    },
    WORK_RANGE_ABOUT_OBJECT: {
        WORK_ABOUT_LIST: [],
        WORK_ABOUT_PAGE: 1
    },
    WORK_RANGE_MINE_OBJECT: {
        WORK_MINE_LIST: [],
        WORK_MINE_PAGE: 1
    }
}

export default state



