import Vue from 'vue'

let scrollEvent = () => {
    //
    let pageHeight = Math.max(document.body.scrollHeight, document.body.offsetHeight)
    //
    let viewportHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0
    //
    let scrollHeight = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0
    return (pageHeight - viewportHeight - scrollHeight)
}

let scrollHeight = () => {

    let scrollHeight = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop || 0

    return scrollHeight
}

let debounce = (handler, delay) => {
    var timer;
    return function () {
        var self = this, arg = arguments;
        clearTimeout(timer);
        timer = setTimeout(function () {
        handler.apply(self, arg)
        }, delay)
    }
}

function addEvent(obj, type, fn) {
    if (obj.attachEvent) {
        obj.attachEvent('on' + type, function () {
            fn.call(obj);
        })
    } else {
        obj.addEventListener(type, fn, false);
    }
}

let cssSupport = function (attr, value) {
    var element = document.createElement('div');
    if (attr in element.style) {
        element.style[attr] = value;
        return element.style[attr] === value;
    } else {
        return false;
    }
}

Vue.directive('header-fixed', {
    update: function (el, binding) {
        addEvent(window, 'scroll', () => {
            if (scrollHeight() >= 50 && !cssSupport('position', 'sticky')) {
                el.className = 'fixed-nav nav-container'
            } else {
                el.className = 'nav-container'
            }
        })
    }
})

// Vue.directive('load-more', {
//     update: function (el, binding) {
//         addEvent(window, 'scroll', () => {
//             if (scrollEvent() === 0) {
//                 binding.value()
//             }
//         })
//     }
// })

Vue.directive('deboun', {
    update: function (el, binding) {
        addEvent(el, 'click', debounce(binding.value(), 500))
    }
})



