export default [
    {
        path: "/vipDetail/:id",
        name: "vipDetail",
        component: () => import("@/pages/home/workDetail/vipDetail"),
        meta: {
          title: ''
        }
      },
      {
        path: "/searchWorks",
        name: "searchWorks",
        component: () => import("@/pages/home/searchWorks"),
        meta: {
          title: ''
        },
      },
      {
        path: "/myWork",
        name: "myWork",
        component: () => import("@/pages/mine/myWork"),
        meta: {
          title: ''
        },
        beforeEnter: (to, from, next) => {
          if(!localStorage.getItem('dapeng')){
            next('/mine')
          }else{
            next()
          }
        },
        children: [
          {
            path: "/opended",
            name: "opended",
            component: () => import("@/pages/mine/opended")
          },
          {
            path: "/collected",
            name: "collected",
            component: () => import("@/pages/mine/collected")
          }
        ]
      },
      {
        path: "/selectCourseItem/:courseId/:courseName",
        name: "selectCourseItem",
        component: () => import("@/pages/workArea/selectCourseItem"),
        meta: {
          title: ''
        },
      },
      {
        path: "/home/design/study/:id",
        name: "bannerComp",
        component: () => import("@/pages/home/workDetail/workDetail"),
        meta: {
          title: ''
        },
      },
]


