
import * as types from './mutation-types'
import axios from '../../utils/axios'
import { CARSOUSEL_FIGURE, HOME_API, COURSE_LIST, GET_SEARCH_COURSES,GET_SEARCH_VIP, COURSES_ADD, COURSES_DELETE, SEARCH_COURSES, WORKDETAIL, VIPDETAIL } from '../../api/home-api'

let actions = {
  async [types.BANNER_DESIGN]({ commit }, params) {
    try {
      let data = await axios.get(CARSOUSEL_FIGURE, { params })
      commit(types.BANNER_DESIGN, data.data.result)
      return data
    } catch (error) {
      console.error(error)
    }
  },
  async [types.GET_HOME_CATEGORY]({ commit }){
    try {
      let data = await axios.get( HOME_API )
      commit(types.GET_HOME_CATEGORY, data)
      return data
    } catch (error) {
      console.error(error)
    }
  },
  async [types.HOME_DATA_OBJECT_LIST]({ commit }, params){
    try {
      let data = await axios.get(COURSE_LIST, { params })
      commit(types.HOME_DATA_OBJECT_LIST, data)
      return data
    } catch (error) {
      console.error(error)
    }
  },
  async [types.GET_SEARCH_COURSES]({ commit }){
    try {
      let data = await axios.get( GET_SEARCH_COURSES )
      commit(types.GET_SEARCH_COURSES, data)
      return data
    } catch (error) {
      console.error(error)
    }
  },
  async [types.GET_SEARCH_VIP]({ commit }, params){
    try {
      
      let data = await axios.get( GET_SEARCH_VIP, {params} )
      commit(types.GET_SEARCH_VIP, data)
      return data
    } catch (error) {
      console.error(error)
    }
  },


  async [types.COURSES_ADD]({ commit }, params){
    try {
      let data = await axios.post( COURSES_ADD, params )
      commit(types.COURSES_ADD, data)
      return data
    } catch (error) {
      console.error(error)
    }
  },
  async [types.COURSES_DELETE]({ commit }, params){
    try {
      let data = await axios.post( COURSES_DELETE, params )
      commit(types.COURSES_DELETE, data)
      return data
    } catch (error) {
      console.error(error)
    }
  },
  async [types.SEARCH_COURSES]({ commit }, params){
    try {
      let data = await axios.get( SEARCH_COURSES, {params} )
      commit(types.SEARCH_COURSES, data)
      return data
    } catch (error) {
      console.error(error)
    }
  },
  async [types.WORKDETAIL]({ commit }, params){
    try {
      let data = await axios.post( WORKDETAIL, params )
      commit(types.WORKDETAIL, data)
      return data
    } catch (error) {
      console.error(error)
    }
  },
  async [types.VIPDETAIL]({ commit }, params){
    try {
      let data = await axios.get( VIPDETAIL, {params} )
      commit(types.VIPDETAIL, data)
      return data
    } catch (error) {
      console.error(error)
    }
  },
}

export default actions


