import * as types from './mutation-types'

let mutations = {
    [types.WORK_RANGE_HEAT_OBJECT] (state, info) {
        let imgObj = null
        if (info.data.list.length > 1) {
            // 
            info.data.list.forEach(element => {
                // 
                if (element.imgSmall.split(',').length === 1) {
                    // 
                    imgObj = new Image()
                    imgObj.src = element.imgSmall
                    // 
                    imgObj.onload = () => {
                        element['width'] = imgObj.width
                        element['height'] = imgObj.height
                    }
                }
            })
            
            if(state.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST.length > 0 && info.data.list.length > 0){
                for (let index = 0; index < info.data.list.length; index++) {
                    // if(JSON.stringify(state.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST).indexOf(JSON.stringify(info.data.list[index])) === -1){
                    state.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST.push(info.data.list[index])
                    // }
                }
            }else{
                state.WORK_RANGE_HEAT_OBJECT.WORK_HEAT_LIST = info.data.list
            }
        }
    },
    [types.WORK_RANGE_COMM_OBJECT](state, info) {
        let imgObj = null
        if(info.data.list.length > 1){
            // 
            info.data.list.forEach(element => {
            // 
                if(element.imgSmall.split(',').length === 1){
                    // 
                    imgObj = new Image()
                    imgObj.src = element.imgSmall
                    // 
                    imgObj.onload = () => {
                        element['width'] = imgObj.width
                        element['height'] = imgObj.height
                    }
                }
            })

            if(state.WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST.length > 0 && info.data.list.length > 0){
                for (let index = 0; index < info.data.list.length; index++) {
                    if(JSON.stringify(state.WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST).indexOf(JSON.stringify(info.data.list[index])) === -1){
                        state.WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST.push(info.data.list[index])
                    }
                }
            }else{
                state.WORK_RANGE_COMM_OBJECT.WORK_COMM_LIST = info.data.list
            }
        }
    },
    [types.WORK_RANGE_ABOUT_OBJECT](state, info) {
        if(info.data.result.list.length > 0){
            info.data.result.list.forEach(ele => {
                if(ele.approvedAudio){
                    ele['playStatus'] = false
                }
            })
        }
        if(info.data.result.list && info.data.result.list.length > 0){
            state.WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST = state.WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST.concat(info.data.result.list)
        }
    },
    [types.WORK_RANGE_MINE_OBJECT](state, info) {
        let imgObj = null
        if(info.data.list && info.data.list.length > 0){
            // 
            info.data.list.forEach(element => {
            // 
            if(element.imgSmall.split(',').length === 1){
                // 
                imgObj = new Image()
                imgObj.src = element.imgSmall
                // 
                imgObj.onload = () => {
                    element['width'] = imgObj.width
                    element['height'] = imgObj.height
                }
            }
            })
            state.WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST = state.WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST.concat(info.data.list)
        }
    }
}

export default mutations


