
import * as types from './mutation-types'

let mutations = {
    [types.BANNER_DESIGN](state, info) {
        state['BANNER_DESIGN_LIST'] = info
    },
    [types.GET_HOME_CATEGORY](state, info) {
        info.data.forEach(element => {
            if (element.parentId == '') {
                state['HOME_CATEGORY_LIST'].push(element)
            }
        })
    },
    [types.HOME_DATA_OBJECT_LIST](state, info){
        // JSON.stringifywatch
        if(Array.isArray(info.data.result) && info.data.result.length > 0  && JSON.stringify(info.data.result) !== JSON.stringify(state['HOME_DATA_OBJECT'].HOME_DATA_LIST)){
            state['HOME_DATA_OBJECT'].HOME_DATA_LIST = state['HOME_DATA_OBJECT'].HOME_DATA_LIST.concat(info.data.result)
        }
    },
    // 
    [types.HOME_HEADER_NAV](state, info){
        state[types.HOME_HEADER_NAV] = info
    },
    //  => 
    [types.HOME_MIDDLE_NAV](state, info){
        state[types.HOME_MIDDLE_NAV] = info
    },
    [types.CLEAN_HOME_DATA](state){
        state.HOME_DATA_OBJECT.HOME_DATA_PAGE = 1
        state.HOME_DATA_OBJECT.HOME_DATA_LIST = []
    },
    [types.GET_SEARCH_COURSES](state,info){
        state[types.GET_SEARCH_COURSES] = info
    },
    [types.GET_SEARCH_VIP](state,info){
        state[types.GET_SEARCH_VIP] = info
    },

    [types.COURSES_ADD](state,info){
        state[types.COURSES_ADD] = info
    },
    [types.COURSES_DELETE](state,info){
        state[types.COURSES_DELETE] = info
    },
    [types.SEARCH_COURSES](state,info){
        state[types.SEARCH_COURSES] = info
    },
    //
    [types.WORKDETAIL](state,info){
        state[types.WORKDETAIL] = info
    },
    [types.VIPDETAIL](state,info){
        state[types.VIPDETAIL] = info
    },
}

export default mutations



