let mutations = {

    cleanAllStatus(state){
      /**
       * state.homeModule
       * state.userModule
       * state.workRangeModule
       */
      for (let key in state.homeModule) {
        if('HOME_DATA_OBJECT' === key){
          state.homeModule[key].HOME_DATA_PAGE = 1
          state.homeModule[key].HOME_DATA_LIST = []
        }
      }
      for (let key in state.userModule) {
        if(key !== 'LOGIN_USER_OBJECT'){
          state.userModule[key] = null
        }
      }
      for (let key in state.workRangeModule) {
        for (let item in state.workRangeModule[key]) {
          if(item.includes('LIST')){
            state.workRangeModule[key][item] = []
          }else if(item.includes('PAGE')){
            state.workRangeModule[key][item] = 1
          }
        }
      }

      //  &&  => 

      for (let key in state.workRangeSavePosition) {
        state.workRangeSavePosition[key] = 0
      }
    },
    /**
     * state.userModule
     */
    cleanMyFriendAndMyFanceStatus(state){
      for (let key in state.userModule.MY_FANCE_OBJECT_LIST) {
        if(key.includes('LIST')){
          state.userModule.MY_FANCE_OBJECT_LIST[key] = []
        }else if(key.includes('PAGE')){
          state.userModule.MY_FANCE_OBJECT_LIST[key] = 1
        }
      }
      
      for (let key in state.userModule.MY_FRIEND_OBJECT_LIST) {
        if(key.includes('LIST')){
          state.userModule.MY_FRIEND_OBJECT_LIST[key] = []
        }else if(key.includes('PAGE')){
          state.userModule.MY_FRIEND_OBJECT_LIST[key] = 1
        }
      }
    },
    modifiedHomeFooterStatus(state, info){
      if(info === 1) return
      state.homeFooterStatus = info
    },
    cleanWorkRange(state, routers){
      if (routers.includes("highestHeat")) {
        routers = "WORK_RANGE_HEAT_OBJECT"
      } else if (routers.includes("latestTime")) {
        routers = "WORK_RANGE_COMM_OBJECT"
      } else if (routers.includes("workRelatedMe")) {
        routers = "WORK_RANGE_ABOUT_OBJECT"
      } else if (routers.includes('myWorkInRange')){
        routers = "WORK_RANGE_MINE_OBJECT"
      }else{
        routers = 'all'
      }
      for (const key in state.workRangeModule) {
        if(routers === 'all'){
          for (const item in state.workRangeModule[key]) {
            if (item.indexOf("PAGE") > -1) {
              state.workRangeModule[key][item] = 1
            } else {
              state.workRangeModule[key][item] = []
            }
          }
          return
        }
        if(key !== routers){
          for (const item in state.workRangeModule[key]) {
            if (item.indexOf("PAGE") > -1) {
              state.workRangeModule[key][item] = 1
            } else {
              state.workRangeModule[key][item] = []
            }
          }
        }
      }
    },
    // 
    changeAudioStatus(state, { approvedAudio, index }){
      // 
      if(approvedAudio === state.audioState.src){
        // 
        if(!state.audioState.paused){
          // 
          state.audioState.pause()
          state.workRangeModule.WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST[index].playStatus = false
        }else{
          // 
          state.audioState.play()
          state.workRangeModule.WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST[index].playStatus = true
        }
      }else{
        // 
        state.audioState.src = approvedAudio
        // 
        state.workRangeModule.WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST.forEach(ele => {
          ele.playStatus = false
        })
        // 
        state.audioState.play()
        state.workRangeModule.WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST[index].playStatus = true
      }
    },
    cleanAboutAndMine(state, routers){
      state.workRangeModule.WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST = []
      state.workRangeModule.WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_PAGE = 1
      // if(!routers.includes('myWorkInRange')){
      //   state.workRangeModule.WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST = []
      //   state.workRangeModule.WORK_RANGE_MINE_OBJECT.WORK_MINE_PAGE = 1
      // }
    },
    // 
    modifyPreviewerPosition(state, info){
      state.previewerPosition = info
    },
    //
    clearList(state){
      state.workRangeModule.WORK_RANGE_MINE_OBJECT.WORK_MINE_LIST = []
    },
  }

  export default mutations
 


