const Mixins = {

    beforeRouteUpdate(to, from, next) {
  
        // asyncData
        let asyncData = this.$options.asyncData
  
        if (asyncData) {
            loading.start()
            asyncData.call(this, {
                store: this.$store,
                route: to
            }).then(() => {
                loading.finish()
                next()
            }).catch(next)
        }
        else {
            next()
        }
    }
  }

export default Mixins


