import * as types from './mutation-types'
import axios from '../../utils/axios'
import { CHANGE_MSG_STATUS, SET_NOTE_STATUS, TIDING_ALL, GET_NEWS_MSG, BETWEEN_TIDINGS, SEND_NOTE } from '../../api/news-api'

let actions = {
    async [types.CHANGE_MSG_STATUS]({ commit }, info){
        try {
            let data = await axios.post(CHANGE_MSG_STATUS, info)
            return data
        } catch (error) {
            console.log(error)
        }
    },

    async [types.SET_NOTE_STATUS]({ commit }, info){
        try {
            let data = await axios.post(SET_NOTE_STATUS, info)
            return data
        } catch (error) {
            console.log(error)
        }
    },
    async [types.TIDING_ALL]({ commit }){
        try {
            let data = await axios.post(TIDING_ALL)
            return data
        } catch (error) {
            console.log(error)
        }
    },
    async [types.GET_NEWS_MSG]({ commit }, info){
        try {
            let data = await axios.post(GET_NEWS_MSG, info)
            return data
        } catch (error) {
            console.log(error)
        }
    },
    async [types.BETWEEN_TIDINGS]({ commit }, info){
        try {
            let data = await axios.get(BETWEEN_TIDINGS, info)
            return data
        } catch (error) {
            console.log(error)
        }
    },
    async [types.SEND_NOTE]({ commit }, info){
        try {
            let data = await axios.get(SEND_NOTE, info)
            return data
        } catch (error) {
            console.log(error)
        }
    }
}

export default actions


